<template>
  <div>

    <slide-component>
      <ul>
        <li>Stora möjligheter att skapa & sprida eget innehåll</li>
      </ul>
    </slide-component>

    <p>
      Något som utmärker information på internet är de stora möjligheterna att kopiera och sprida digitalt innehåll. Detta är särskilt intressant för sociala medier, i vilken informationen hittar <i>dig</i> utan att du aktivt eller medvetetet söker upp den.
    </p>

    <h3>Automatisk spridning genom algoritmer</h3>

    <slide-component>
      <ul>
        <li>Algoritmer styr ett inläggs räckvidd</li>
        <li>Olika typer av engagemang ökar ett inläggs popularitet</li>
        <li>Populära inlägg får större spridning</li>
      </ul>
    </slide-component>

    <p>
      När en användare publicerar innehåll i exempelvis sociala medier så är det respektive plattforms <a-wiki c="algoritm">algoritmer</a-wiki> som avgör vem som kommer att se inlägget. Det kan ofta vara svårt att göra sin röst hörd i informationsbruset. Nya inlägg visas främst för den närmsta kretsen medan populära inlägg har potential att nå en betydligt större publik.
    </p>

    <p>
      Ett sätt för plattformarna att bedöma ett inläggs popularitet är att mäta till vilken grad andra användare interagerar med innehållet. Även om möjligheterna till engagemang skiljer sig från plattform till plattform, så är det vanligt är att låta användare:
    </p>

    <ul>
      <li>
        <span class="semibold">Klicka</span>: klicka på ett inlägg, e.g. för att läsa mer, förstora en bild eller besöka en länkad webbplats.
      </li>
      <li>
        <span class="semibold">Gilla/Reagera</span>: ge snabb, positiv feedback på inlägg, e.g. med en tumme upp eller hjärta. Facebook erbjuder, sedan 2016, ytterligare reaktioner: skratt, arg, ledsen och kärlek.
      </li>
      <li>
        <span class="semibold">Kommentera</span>: svara på ett befintligt inlägg eller en annan användares kommentar.
      </li>
      <li>
        <span class="semibold">Dela</span>: aktivt dela vidare en annan användares inlägg.
      </li>
      <li>
        <span class="semibold">Följa</span>: prenumerera på en annan användares inlägg.
      </li>
    </ul>

    <p>
      Samtliga interaktioner gör att ett inlägg bedöms som mer populärt och spridningen till andra användare ökar. Utöver att låta användare aktivt interagera med inlägg så har plattformarna även möjlighet att bedöma popularitet utifrån passiva handlingar, e.g. hur länge en användare tittar på ett inlägg innan hen scrollar vidare i flödet.
    </p>

    <info-fields class="infoFields">

      <div class="summary">

  			<div class="hBox">
  				SAMMANFATTNING
  			</div>

  			Inlägg som skapar engagemang på sociala medier bedöms av algoritmerna som populära. Dessa visas automatiskt för fler användare och får därmed större spridning.

  		</div>

  	</info-fields>

    <h3>Personligt anpassat innehåll</h3>

    <slide-component>
      <ul>
        <li>Algoritmer analyser ditt surfbeteende</li>
        <li>Bedömer och visar innehåll intressant för just dig</li>
        <li>Ekokammare & filterbubblor</li>
        <li>Var kritisk till urvalet</li>
      </ul>
    </slide-component>

    <p>
      När du surfar på sociala medier så ser du endast en minimal bråkdel av allt material som publiceras, beräknat till omkring en halv miljon Snapchatbilder, nästan lika många Twitterinlägg, och nära 50.000 Instagrambilder, <a href="https://www.domo.com/learn/data-never-sleeps-5" title="Data Never Sleeps 2.0
" target="_blank">varje minut</a>. Även av det material som dina vänner publicerar, kommer majoriteten aldrig att visas för dig.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="image">
          <a href="https://www.domo.com/learn/data-never-sleeps-5" title="Data Never Sleeps 2.0
" target="_blank" class="nohover">
            <img src="https://web-assets.domo.com/blog/wp-content/uploads/2017/07/17_domo_data-never-sleeps-5-01.png"
           alt="infographic" />
         </a>
        </div>
        <div class="caption">
          Så mycket material publicerades på olika sociala plattformar, varje minut under 2017.
        </div>
        <div class="source">
          Källa: <a href="https://www.domo.com/learn/data-never-sleeps-5" title="Data Never Sleeps 2.0
" target="_blank">Data Never Sleeps 5.0, Domo</a>
        </div>
      </div>
    </media-container>

    <p>
      För att behålla dig som användare är det viktigt att plattformen erbjuder en bra upplevelse. En viktig del i detta är att presentera sådant innehåll som bedöms vara intressant för just dig. På samma sätt som plattformarnas algoritmer bedömer ett inläggs popularitet så arbetar dessa även för att skapa en förståelse för vem du är och vad du gillar.
    </p>

    <p>
      Innehåll som bedöms som relevant för dig kommer att visas, medan andra inlägg aldrig kommer att nå dig. Klickar du ofta på material som delas av en specifik vän så kommer du att se mer av hens inlägg, samt saker som hen har interagerat med. Reagerar du ofta på inlägg från politiska Facebooksidor kommer du att presenteras för mer liknande politiskt innehåll. Slutar du scrolla när det kommer en video med <a href="https://www.youtube.com/watch?v=we9_CdNPuJg" title="Fainting goats" target="_blank">svimmande getter </a> så kommer du att få se fler söta djurklipp.
    </p>

    <p>
      Kort och gott: istället för att du aktivt söker upp innehållet, såsom när du använder en sökmotor, så hittar innehållet i sociala medier dig. Du får helt enkelt ett personligt flöde.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <iframe src="https://www.facebook.com/plugins/video.php?href=https%3A%2F%2Fwww.facebook.com%2Fsvt%2Fvideos%2F1278934345482484%2F&show_text=0&width=560" width="560" height="315" style="border:none;overflow:hidden" scrolling="no" frameborder="0" allowTransparency="true" allowFullScreen="true"></iframe>
        </div>
        <div class="caption">
          SVT:s Sociala Monster illustrerar hur sociala mediers algoritmer arbetar för att ge oss personliga flöden.
        </div>
        <div class="source">
          Källa: <a href="https://www.facebook.com/svt/videos/1278934345482484/" title="Filterbubblan på två minuter" target="_blank">SVT (Facebook)</a>
        </div>
      </div>
    </media-container>

    <p>
      I en miljö där allt material är särskilt utvalt för dig är det värt att fundera:

      <ul>
        <li>
          Varför presenteras <i>just den här informationen</i> för mig?
        </li>
        <li>
          Vad får jag <i>inte</i> se, och vad får andra personer se?
        </li>
      </ul>

    </p>

    <p>
      De <a href="https://fof.se/artikel/forskare-filterbubblan-ar-en-myt" title="Forskare sticker hål på myten om filterbubblor" target="_blank">omstridda</a> begreppen <a-wiki c="Filterbubbla">filterbubbla</a-wiki> och <a-wiki c="Ekokammare">ekokammare</a-wiki> beskriver hur vi på nätet, medvetet eller omedvetet som en följd av nätets algoritmer, tenderar att sluta in oss i informationsbubblor med ensidiga perspektiv. En konsekvens av detta är att vi isolerar oss från åsikter vi inte delar och som utmanar vår världsbild.
    </p>

    <p>
      Oavsett filterbubblornas faktiska inverkan kvarstår det faktum att ditt flöde på sociala medier är skräddarsytt för att passa just dig. Återigen är det värt att fundera:

      <ul>
        <li>
          Hur nyanserade är mina sociala flöden?
        </li>
        <li>
          Hur påverkas jag av innehållet?
        </li>
        <li>
          Hur påverkas andra personer av <i>sitt</i> personliga flöde?
        </li>
      </ul>

    </p>

    <p>

      Särskilt när du presenteras för åsikter som du <i>inte</i> delar kan du resonera kring:

      <ul>
        <li>
          Varför får jag se <i>just detta inlägg</i>?
        </li>
        <li>
          Har inlägget en informerande eller en provocerande ton?
        </li>
        <li>
          Hur har andra reagerat på inlägget? Är folk glada eller arga?
        </li>
        <li>
          Är detta inlägg representativt, eller är det utvalt för att det har skapat många reaktioner?
        </li>
        <li>
          Finns det mer <i>nyanserade</i> perspektiv som jag inte presenteras för?
        </li>
      </ul>
    </p>

    <info-fields class="infoFields">

      <div class="excercise">

        <div class="hBox">
          TESTA SJÄLV: ANALYSERA DITT FLÖDE
        </div>

        <p>
          Besök de sidor eller sociala plattformar där du tar del av nyheter. Bilda dig en överblick av de nyheter som presenteras, genom att läsa rubriker och eventuella ingresser.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Vilken typ av nyheter dyker upp i ditt flöde?
          </li>
          <li>
            Tar du del av nyheter från flera källor? Är dessa oberoende eller presenterar de liknande perspektiv?
          </li>
          <li>
            Tar du ofta aktivt del av ett flera perspektiv kring en nyhet?
          </li>
        </ul>

      </div>

      <div class="excercise">

        <div class="hBox">
          TESTA SJÄLV: SYNLIGGÖR ALGORITMERNA
        </div>

        <p>
          Besök en social plattform som du är med i. Välj ut ett par inlägg som du tycker är extra intressanta.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Diskutera utifrån frågeställningarna i kapiteltexten ovan.
          </li>
          <li>
            Hur har algoritmerna arbetat för att välja ut informationen åt mig?
          </li>
        </ul>

      </div>

      <div class="summary">

  			<div class="hBox">
  				SAMMANFATTNING
  			</div>

        <p>
          Utifrån hur du använder sociala medier skapar sig respektive plattforms algoritmer en förståelse för vem du är och vad du gillar. Denna kunskap används för att presentera utvalda inlägg som bedöms som intressanta för just dig. Dina vänner har egna personliga flöden med annat innehåll än vad du ser.
        </p>

  		</div>

    </info-fields>

    <h3>Virala inlägg</h3>

    <slide-component>
      <ul>
        <li>Oproportionerligt stor spridning</li>
        <li>Spelar på mottagarens känslor</li>
        <li>Delas oreflekterat vidare</li>
      </ul>
    </slide-component>

    <p>
      Ibland får inlägg eller nyheter så pass stor spridning i sociala medier och andra forum att de dyker upp i var och varannan persons nyhetsflöde. Sådana inlägg, som framförallt sprids i sociala nätverk och som ofta får oproportionerligt stor spridning i förhållande till sitt nyhetsvärde, kallas för virala. Det kan handla om enskilda inlägg som skapar stort engagemang, såväl som nyhetshändelser som genererar en stor mängd inlägg från olika håll.
    </p>

    <p>
      Det är inte ovanligt att inlägg som får stor spridning spelar på folks känslor. Innehåll som får dig att känna är mer troliga att du kommer gilla, reagera på, kommentera eller dela vidare. Den spontana känslan kan vara så stark att du i all välmening delar ett inlägg, video eller artikel vidare, utan att ha reflekterat över - eller ens tagit del av hela - innehållet. Inte sällan handlar det om innehåll som får dig som läsare att känna rädsla eller bli upprörd.
    </p>

    <p>
      I videon nedan illustrerar <a href="https://www.metro.se/viralgranskaren" title="Viralgranskaren" target="_blank">Metros Viralgranskaren</a> och <a href="https://www.iis.se/" title="Internetstiftelsen i Sverige" target="_blank">Internetstiftelsen i Sverige</a> hur en falsk nyhet hösten 2016 snabbt blev viral och skapade ilska i sociala medier. I videon jämförs hur personer <i>med</i> respektive <i>utan</i> källkritisk blick kan tänkas ha reagerat på bluffen.
    </p>

    <media-container class="mediaContainer">
      <div>
        <div class="content">
          <iframe width="560" height="315" src="https://www.youtube.com/embed/wc353OW5kW0" frameborder="0" allowfullscreen></iframe>
        </div>
        <div class="caption">
          Viralgranskaren och Internetstiftelsen jämför hur två olika personer hanterar en viral nyhet, med respektive utan ett källkritisk tänkande.
        </div>
        <div class="source">
          Källa: <a href="https://www.youtube.com/watch?v=wc353OW5kW0" title="Youtube: Källkritik på nätet är viktigare än någonsin" target="_blank">MetroSverige (Youtube)</a>
        </div>
      </div>
    </media-container>

    <info-fields class="infoFields">

      <div class="excercise">

        <div class="hBox">
          TESTA SJÄLV: VIRALA NYHETER
        </div>

        <p>
          Besök <a href="https://www.metro.se/om/%C3%A5rslista" title="Viralgranskaren: Årslistor" target="_blank">Viralgranskarens årslistor</a> för att läsa mer om de senaste årens granskningar och trender. Klicka vidare på valfritt årtal för att lista årets samtliga granskningar.
        </p>

        <div class="hBox">
          DISKUTERA
        </div>

        <ul>
          <li>
            Känner du igen någon eller några av de virala nyheterna?
          </li>
          <li>
            Har du själv klickat, spridit och/eller trott på något av de falska påståenden som har spridits?
          </li>
          <li>
            Hur agerar du när en viral nyhet som du själv har spridit visar sig vara falsk?
          </li>
        </ul>

      </div>

      <div class="summary">

  			<div class="hBox">
  				SAMMANFATTNING
  			</div>

        <p>
          Virala inlägg och nyheter är sådana som får oproportionerligt stor spridning i sociala medier. Dessa spelar ofta på läsarens känslor som delar inlägget vidare utan att kritiskt reflektera över innehållet.
        </p>

  		</div>


      <div class="more">

        <div class="hBox">
          FÖRDJUPNING
        </div>

        <p>
          Metros <a href="https://www.metro.se/viralgranskaren" title="Viralgranskaren" target="_blank">Viralgranskaren</a> granskar sedan 2014 nyheter och påståenden som har blivit virala i sociala medier. Hos <a href="https://www.metro.se/lilla-viralgranskaren" title="Lilla Viralgranskaren" target="_blank">Lilla Viralgranskaren</a> för barn 6-18 år hittar du skolmaterial och interaktiva uppgifter kring virala nyheter.
        </p>

      </div>

    </info-fields>

  </div>
</template>

<script>

import AWiki from '@/components/AWiki.vue'
import InfoFields from '@/components/learn/InfoFields.vue'
import MediaContainer from '@/components/learn/MediaContainer.vue'
import SoiGraph from '@/components/SoiGraph.vue'
import SlideComponent from '@/components/learn/SlideComponent.vue'

export default {
  components: {
    AWiki,
    InfoFields,
    MediaContainer,
    SoiGraph,
    SlideComponent
  },
  data () {
    return {
      'header': 'Spridning och virala inlägg'
    }
  },
  created () {

  }
}
</script>

<style>
</style>



